module.exports = (param) => {

	var url = param.url;
	var method = param.method;
	var header = param.header || {};
	var data = param.data || {};


	//token
	const userInfo = uni.getStorageSync('Token'); //token
	if (userInfo && userInfo.token) {
		const token = userInfo.token //token
		header['Authorization'] = token;
	}

	// : GET POST 
	if (method) {
		method = method.toUpperCase(); // 
		if (method == "POST") {
			header["content-type"] = "application/json";
		}
	}

	//  
	if (!param.hideLoading) {
		uni.showLoading({
			title: "..."
		})
	}



	// 
	uni.request({
		url: url,
		method: method || "POST",
		header: header,
		data: data,
		success: res => {
			//console.log(res);
			// if (res.data.code && res.data.code != 200) { // api
			// 	uni.showModal({
			// 		content: res.message
			// 	})
			// 	return;
			// }
			
			if (res.data.code && res.data.code == 10001) {
				uni.showModal({
					title: '',
					content: '',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: "/pages/user/login"
							});
						} else if (res.cancel) {
							// console.log('');
						}
					}
				});
			}
			
			
			typeof param.success == "function" && param.success(res.data);
		},
		fail: (e) => {
			uni.showModal({
				content: e.meg
			})
			typeof param.fail == "function" && param.fail(e.data);
		},
		complete: () => {
			//console.log("complete");
			uni.hideLoading();
			typeof param.complete == "function" && param.complete(e.data);
			return;
		}
	})

}
