//vuevuex
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({  
    state: {  
        uerInfo: {},  
        hasLogin: false  
    },  
    mutations: {  
        login(state, provider) {//  
            state.hasLogin = true  
            state.uerInfo.token = provider.token
            state.uerInfo.userName = provider.client.user_name
            state.uerInfo.userId = provider.client.uid //id
			state.uerInfo.companyName = provider.client.profiles.user_company.company_name
			state.uerInfo.user_Id =provider.client.profiles.user_info.user_id //Id
			state.uerInfo.user_Type = provider.client.profiles.user_info.type //
			state.uerInfo.isIdentifiy = provider.client.profiles.user_info.is_identifiy //
            uni.setStorage({//  
                key: 'uerInfo',  
                data: provider  
            })  
        },  
        logout(state) {//  
            state.hasLogin = false  
            state.uerInfo = {}  
            uni.removeStorage({  
                key: 'uerInfo'  
            })  
        }  
    }  
})
export default store